.hero-about {
  margin-bottom: 60px;

  .photos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    margin-top: 40px;
    img {
      border-radius: 10px;
      height: 100%;
      width: 100%;
    }

    @media (max-width: 767px) {
      grid-template-columns: 1fr 1fr;
    }
  }
}

.about-services {
  margin-bottom: 60px;
  h2 {
    margin-bottom: 30px;
  }

  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    @media (max-width: 767px) {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    display: grid;
    grid-template-columns: 0.5fr 1fr;
    gap: 10px;
    @media (max-width: 767px) {
      grid-template-columns: 1fr;
    }
    &:nth-child(1) {
      grid-column: span 2;
      @media (max-width: 767px) {
        grid-column: auto;
      }
    }

    img {
      border-radius: 10px;
      width: 100%;
      height: 100%;
    }

    .content {
      background: var(--fon);
      padding: 40px;
      border-radius: 8px;

      .subtitle {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        text-transform: uppercase;
        color: var(--chernyy);
        margin-bottom: 14px;
      }
      p {
        margin: 0;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--chernyy);
      }
    }
  }
}

.about-seo {
  margin-bottom: 60px;
  .wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    @media (max-width: 767px) {
      grid-template-columns: 1fr;
    }
  }

  .right img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
  }
}
