.videos {
  margin-bottom: 100px;
  &__wrapper {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: clamp(20px, 3vw, 40px);
  }

  &__tags {
    margin-top: 30px;
    margin-bottom: 20px;
    display: flex;
    gap: 5px;
    flex-wrap: wrap;
    .tag {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);
      border-radius: 8px;
      padding: 8px 20px;
      max-width: fit-content;
      background: var(--fon);
    }
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: 14px;

    .video {
      position: relative;

      video {
        max-width: 100%;
        height: auto;
        object-fit: cover;
        border-radius: 10px;
      }
    }

    p {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      color: var(--chernyy);
      margin: 0;
    }

    .video-btn-play {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 90px;
      height: 90px;
      display: grid;
      place-items: center;
      background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
      border-radius: 50%;
      transition: 0.3s ease;
      will-change: opacity;

      @media (any-hover: hover) {
        &:hover {
          opacity: 0.5;
        }
      }
    }
  }
}
