.contacts-hero {
  margin-bottom: 30px;
}

.contacts-main {
  margin-bottom: 60px;
  .contacts-main-nav {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
    overflow: auto;
    .btn {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      color: var(--chernyy);
      border: 1px solid var(--siniy);
      border-radius: 8px;
      padding: 12px 40px;
      transition: 0.3s ease;
      @media (max-width: 767px) {
        flex-shrink: 0;
      }
      &.active {
        color: #fff;
        background: var(--siniy);
      }

      @media (any-hover: hover) {
        &:hover {
          color: #fff;
          background: var(--siniy);
        }
      }
    }
  }

  .contacts-tab {
    display: grid;
    grid-template-columns: 375px 1fr;
    gap: 10px;
    @media (max-width: 767px) {
      grid-template-columns: 1fr;
    }
    .left {
      display: flex;
      flex-direction: column;
      border-radius: 8px;
      padding: 14px;
      background: var(--fon);
      gap: 14px;
      img {
        border-radius: 8px;
      }

      .item {
        display: grid;
        grid-template-columns: 16px 1fr;
        gap: 8px;

        .info {
          display: flex;
          flex-direction: column;
          gap: 4px;

          span {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 12px;
            color: var(--vspomogatenye-elementy);
          }
          p,
          a {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 14px;
            line-height: 130%;
            color: var(--chernyy);
            text-decoration: none;
            margin: 0;
          }
        }
      }
    }
  }

  .contacts-main-rec {
    margin-top: 30px;
    padding: 30px;
    background: var(--fon);
    border-radius: 8px;

    .subtitle {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 18px;
      line-height: 120%;
      text-transform: uppercase;
      color: var(--chernyy);
      margin-bottom: 20px;
    }

    ul {
      display: grid;
      grid-template-columns: 1fr 1fr;
      row-gap: 14px;
      column-gap: 40px;
      @media (max-width: 767px) {
        grid-template-columns: 1fr;
      }
      li {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;

        span {
          position: relative;
          font-family: var(--font-family);
          font-weight: 700;
          font-size: 16px;
          line-height: 120%;
          color: var(--chernyy);
          flex: 1 1 auto;
        }

        p {
          margin: 0;
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 14px;
          line-height: 130%;
          color: var(--chernyy);
        }
      }
    }
  }
}
