.product {
  margin-bottom: 40px;
  &__title {
    margin-bottom: 40px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    @media (max-width: 1024px) {
      grid-template-columns: 1fr;
    }
  }

  .photo {
    border-radius: 8px;

    img {
      border-radius: 8px;
      height: 100%;
      width: 100%;
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    gap: 10px;

    .top {
      background: var(--fon);
      border-radius: 8px;
      padding: 20px 40px;
      @media (max-width: 767px) {
        padding: 20px 10px;
      }
    }
    .bottom {
      background: var(--fon);
      border-radius: 8px;
      padding: 20px 40px;
      @media (max-width: 767px) {
        padding: 20px 10px;
      }
    }
  }

  &__options {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 30px;
    span {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      color: var(--chernyy);
    }
    &-block {
      position: relative;
      input {
        opacity: 0;
        position: absolute;
        inset: 0;
        appearance: none;
        z-index: 1;
        cursor: pointer;
      }
      label {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--chernyy);
        border-radius: 8px;
        padding: 8px 20px;
        background: #e3e8f3;
        display: block;
      }

      input:checked + label {
        background: var(--siniy);
        color: #fff;
      }
    }
  }

  &__counter {
    display: flex;
    align-items: center;
    gap: 14px;
    margin-bottom: 14px;
    span {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      color: var(--chernyy);
    }

    .counter {
      display: flex;
      align-items: center;
      gap: 6px;
    }

    button {
      width: 40px;
      height: 40px;
      display: grid;
      place-items: center;
      border-radius: 4px;
      background: #e3e8f3;
    }

    input {
      max-width: fit-content;
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 18px;
      line-height: 120%;
      text-transform: uppercase;
      color: var(--chernyy);
      background: transparent;
      border: none;
      width: 50px;
      text-align: center;
    }
  }

  &__price {
    padding: 14px;
    border-radius: 8px;
    background: #fff;
    position: relative;
    &-block {
      display: flex;
      align-items: center;
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 32px;
      text-transform: uppercase;
      color: var(--chernyy);
      gap: 20px;
    }
    .old-price {
      display: flex;
      align-items: center;
      gap: 5px;
      span {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        text-decoration: line-through;
        color: var(--vspomogatenye-elementy);
      }
      .sale {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 14px;
        color: var(--chernyy);
      }
    }
    p {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 12px;
      color: var(--chernyy);
    }

    .info {
      position: absolute;
      top: 20px;
      right: 20px;
      display: flex;
      align-items: center;
      gap: 4px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);

      @media (max-width: 992px) {
        position: static;
      }
    }
  }

  &__character {
    .subtitle {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      color: var(--chernyy);
      margin-bottom: 14px;
    }

    .list {
      display: flex;
      flex-direction: column;
      gap: 8px;
      margin-bottom: 20px;
      li {
        display: flex;
        justify-content: space-between;
        gap: 10px;
        span {
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 14px;
          line-height: 130%;
          color: var(--vspomogatenye-elementy);
        }
        p {
          margin: 0;
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 14px;
          line-height: 130%;
          color: var(--chernyy);
        }
      }
    }

    .delivery {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 4px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: #23211f;
    }

    .actions {
      display: flex;
      align-items: center;
      gap: 10px;
      margin-top: 10px;
      @media (max-width: 992px) {
        flex-direction: column;
      }
      button {
        width: 100%;
        padding: 12px;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--belyy);
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        gap: 5px;
      }

      .btn--border {
        border: 1px solid var(--siniy);
        border-radius: 8px;
        color: var(--chernyy);
        transition: 0.3s ease;
        @media (any-hover: hover) {
          &:hover {
            background: var(--siniy);
            color: #fff;
          }
        }
      }
      .btn--compare {
        color: var(--chernyy);
        max-width: fit-content;
        transition: 0.3s ease;

        @media (any-hover: hover) {
          &:hover {
            color: var(--siniy);
          }
        }
      }
    }
  }
}

.product-info {
  margin-bottom: 60px;
  &__nav {
    margin-bottom: 20px;
    .list {
      display: flex;
      gap: 10px;
      overflow: auto;
      li {
        @media (max-width: 992px) {
          flex-shrink: 0;
        }
      }
      .btn {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: var(--chernyy);
        border: 1px solid var(--siniy);
        border-radius: 8px;
        padding: 12px 40px;
        transition: 0.3s ease;
        @media (any-hover: hover) {
          &:hover {
            background: var(--siniy);
            color: #fff;
          }
        }

        &.active {
          background: var(--siniy);
          color: #fff;
        }
      }
    }
  }

  .use {
    &__wrapper {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 20px;
      @media (max-width: 1024px) {
        grid-template-columns: 1fr;
      }
    }

    &__items {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 20px;
      @media (max-width: 1024px) {
        grid-template-columns: 1fr 1fr;
      }
    }

    &__item {
      position: relative;
      padding: 14px;
      .image {
        position: absolute;
        inset: 0;
        z-index: -1;
        overflow: hidden;
        border-radius: 8px;
        img {
          width: 100%;
          height: 100%;
          border-radius: 8px;
        }
      }

      .title {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: clamp(14px, 2vw, 16px);
        line-height: 120%;
        color: var(--belyy);
        padding-top: 84px;
      }
    }

    &__cb {
      border-radius: 8px;
      padding: 14px 14px 14px 20px;
      background: var(--fon);
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
      gap: 20px;
      margin-top: 10px;
      p {
        margin: 0;
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        text-transform: uppercase;
        color: var(--chernyy);
      }

      .btn--accent {
        padding: 12px;
        max-width: 170px;
        width: 100%;
        @media (max-width: 640px) {
          max-width: 100%;
        }
      }
    }

    &__video {
      background: var(--fon);
      border-radius: 8px;
      padding-bottom: 20px;
      height: 100%;
      display: flex;
      flex-direction: column;
      .video {
        margin-bottom: 14px;
        height: 100%;
      }
      video {
        border-radius: 8px;
        max-width: 100%;
        height: 100%;
        object-fit: cover;
      }
      .desc {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: var(--chernyy);
        padding-inline: 14px;
        margin-top: auto;
      }
    }
  }
}

.product-view {
  margin-bottom: 60px;
  .title__section {
    margin-bottom: 30px;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 10px;
    @media (max-width: 1560px) {
      grid-template-columns: repeat(4, 1fr);
    }
    @media (max-width: 1140px) {
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }
  }
}

.product-futures {
  .title__section {
    margin-bottom: 30px;
  }
}
