.blog {
  margin-bottom: 60px;
}

.articles {
  margin-bottom: 60px;
  &-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 20px;
    a {
      display: block;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);
      background: var(--fon);
      padding: 8px 20px;
      border-radius: 8px;

      transition: 0.3s ease;

      @media (any-hover: hover) {
        &:hover {
          background: var(--siniy);
          color: #fff;
        }
      }

      &.active {
        background: var(--siniy);
        color: #fff;
      }
    }
  }

  &-search {
    position: relative;
    margin-bottom: 20px;
    .input {
      border: 1px solid var(--divayder);
      border-radius: 8px;
      padding: 15px 20px;
      width: 100%;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--vspomogatenye-elementy);
    }

    .articles-search-btn {
      position: absolute;
      top: 50%;
      right: 5px;
      transform: translateY(-50%);
      width: 40px;
      height: 40px;
      display: grid;
      place-items: center;
      border-radius: 4px;
      background: var(--fon);

      transition: 0.3s ease;
      @media (any-hover: hover) {
        &:hover {
          background: var(--siniy);
          svg path {
            fill: #fff;
          }
        }
      }
    }
  }

  .articles-items-main {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    margin-bottom: 10px;
    @media (max-width: 767px) {
      grid-template-columns: 1fr;
    }
  }

  .articles-items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    @media (max-width: 767px) {
      grid-template-columns: 1fr;
    }
  }

  .article-item {
    display: flex;
    flex-direction: column;
    position: relative;

    background: #000;
    background: var(--fon);
    border-radius: 8px;
    overflow: hidden;
    padding-bottom: 20px;

    &-link {
      position: absolute;
      inset: 0;
      z-index: 1;
    }

    .img {
      margin-bottom: 14px;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        border-radius: 8px;
        transition: scale 0.3s ease;
      }
    }

    .date {
      padding-inline: 14px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 12px;
      color: var(--chernyy);
      margin-bottom: 8px;
    }
    .title {
      padding-inline: 14px;
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      color: var(--chernyy);
      transition: color 0.3s ease;
    }
    @media (any-hover: hover) {
      &:hover {
        img {
          scale: 1.2;
        }
        .title {
          color: var(--siniy);
        }
      }
    }
  }
  .articles-items-bottom {
    max-width: 260px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 20px;
    .link-more {
      border: 1px solid var(--siniy);
      border-radius: 8px;
      padding: 12px 40px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);
      transition: 0.3s ease;
      display: block;
      text-align: center;
      @media (any-hover: hover) {
        &:hover {
          background: var(--siniy);
          color: #fff;
        }
      }
    }

    .pagination {
      display: flex;
      gap: 4px;

      .btn {
        border: 1px solid var(--siniy);
        border-radius: 4px;
        width: 40px;
        height: 40px;
        display: grid;
        place-items: center;
        cursor: pointer;
        svg {
          pointer-events: none;
        }
      }

      .list {
        display: flex;
        gap: 4px;

        .list-number {
          width: 40px;
          height: 40px;
          display: grid;
          place-items: center;
          border: 1px solid var(--siniy);
          border-radius: 4px;
          transition: 0.3s ease;

          @media (any-hover: hover) {
            &:hover {
              background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
              border-color: transparent;
            }
          }
        }
      }
    }
  }
}
