.compare-hero {
  h1 {
    margin-bottom: 30px;
  }

  .tags {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    margin-bottom: 20px;
    .tag {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);
      border-radius: 8px;
      padding: 8px 20px;
      background: var(--fon);
      max-width: fit-content;
    }
  }

  .actions {
    display: flex;
    gap: 20px;
    align-items: center;
    margin-bottom: 30px;
    .btn-action {
      display: flex;
      align-items: center;
      gap: 10px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);
    }
  }

  .toggler-btn {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
  }

  .toggler {
    position: absolute;
    inset: 0;
    opacity: 0;
    appearance: none;
    z-index: 1;
    cursor: pointer;
    margin: 0;
  }
  .toggler-custom {
    width: 50px;
    height: 28px;
    border-radius: 1000px;
    background: #f8f8f8;
    position: relative;
    transition: 0.3s ease;
    &:before {
      content: "";
      position: absolute;
      width: 22px;
      height: 22px;
      top: 50%;
      left: 2px;
      transform: translateY(-50%);
      background: var(--siniy);
      border-radius: 100%;
      transition: 0.3s ease;
    }
  }

  .toggler:checked + .toggler-custom {
    background: var(--siniy);
    &:before {
      left: auto;
      right: 2px;
      background: #fff;
    }
  }
}

// table

.compare-table {
  margin-bottom: 60px;
  .compare-table-items {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 20px;
    margin-bottom: 34px;
  }

  .table {
    display: grid;

    .table-item {
      padding-block: 14px;
      border-top: 1px solid var(--divayder);
      border-bottom: 1px solid var(--divayder);
      display: flex;
    }
    .table-block {
      display: flex;
      flex-direction: column;
      gap: 8px;
      max-width: 600px;
      width: 100%;
      span {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 14px;
        color: var(--chernyy);
      }
      p {
        margin: 0;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--chernyy);
      }
    }

    .table-block-text {
      display: flex;
      gap: 10px;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
}

// recommend

.compare-recommend {
  margin-bottom: 60px;
  h2 {
    margin-bottom: 30px;
  }

  .compare-recommend__items {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 10px;
  }
}

.compare-futures {
  h2 {
    margin-bottom: 40px;
  }
}
