.object {
  &__wrapper {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: 40px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__aside {
    @media (max-width: 992px) {
      display: none;
    }
  }

  &__carts {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    margin-top: 30px;
  }

  &__cart {
    display: flex;
    flex-direction: column;
    gap: 10px;

    .image {
      img {
        border-radius: 8px;
      }
    }

    .title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      color: var(--chernyy);
      margin-bottom: 4px;
    }

    .list {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 10px;

      li {
        display: flex;
        flex-direction: column;
        gap: 4px;

        span {
          display: block;
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 12px;
          color: var(--vspomogatenye-elementy);
          padding-bottom: 4px;
          border-bottom: 1px solid var(--divayder);
        }

        p {
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 14px;
          line-height: 130%;
          color: var(--chernyy);
          margin: 0;
        }
      }
    }
  }
}
