.areas {
  &__wrapper {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: 40px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__aside {
    @media (max-width: 992px) {
      display: none;
    }
  }

  .banner-section__btn {
    background: var(--siniy);
    color: #fff;
    @media (any-hover: hover) {
      &:hover {
        background: transparent;
        color: #000;
      }
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    .banner-section {
      margin-bottom: 30px;

      .banner-section__img img {
        margin: 0;
      }
    }
    strong {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      color: var(--chernyy);
      margin-bottom: 14px;
    }

    .list {
      display: flex;
      flex-direction: column;
      gap: 8px;
      margin-bottom: 30px;
      li {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--chernyy);
        position: relative;
        padding-left: 16px;
        &:before {
          content: "";
          position: absolute;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
          width: 8px;
          height: 8px;
          background: var(--siniy);
          border-radius: 50%;
        }
      }
    }

    p {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);
      margin-bottom: 14px;
    }

    img {
      width: 100%;
      display: block;
      margin-bottom: 30px;
    }
  }

  .areas-docs {
    position: relative;
    background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
    border-radius: 8px;
    padding: clamp(10px, 2vw, 20px);
    margin-bottom: 60px;
    .areas-docs-content {
      display: flex;
      flex-direction: column;
      gap: 20px;
      max-width: 600px;
      position: relative;
      z-index: 1;

      @media (max-width: 992px) {
        max-width: 300px;
      }
    }
    .areas-docs-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: clamp(22px, 3vw, 28px);
      text-transform: uppercase;
      color: var(--chernyy);
    }

    .areas-docs-btn {
      background: var(--siniy);
      color: #fff;
      max-width: 180px;
      padding-block: 12px;
      border: 1px solid transparent;
      @media (any-hover: hover) {
        &:hover {
          background: var(--hover-golubogo);
          color: var(--siniy);
          border: 1px solid var(--siniy);
        }
      }
    }

    .areas-docs-image {
      position: absolute;
      bottom: 0;
      right: 50px;
      @media (max-width: 992px) {
        max-width: 250px;
        right: 0;
      }
      @media (max-width: 767px) {
        max-width: 160px;
      }
      img {
        margin: 0;
      }
    }
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    margin-bottom: 60px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
    .item {
      display: flex;
      flex-direction: column;
      gap: 10px;
      position: relative;
      padding: 70px 14px 20px 14px;
      overflow: hidden;
      border-radius: 8px;
      height: 180px;
      .item-image {
        position: absolute;
        inset: 0;
        z-index: -1;
        img {
          width: 100%;
          height: 100%;
          border-radius: 8px;
        }
      }

      .item-title {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: var(--belyy);
        max-width: 220px;
        margin-top: auto;
      }
      .item-link {
        max-width: fit-content;
        border-radius: 8px;
        padding: 11px 20px;
        background: var(--siniy);
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--belyy);
        display: none;
      }

      @media (any-hover: hover) {
        &:hover {
          .item-link {
            display: block;
          }
        }
      }
    }
  }
}
