.catalog {
  margin-bottom: 60px;
  &__wrapper {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: clamp(20px, 3vw, 40px);

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__tags {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 8px;
    margin-bottom: 30px;
    p {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 14px;
      color: var(--chernyy);
      margin: 0;
    }
  }

  &__tag {
    border-radius: 8px;
    padding: 8px 10px;
    background: var(--fon);
    max-width: fit-content;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);

    transition: 0.3s ease;

    @media (any-hover: hover) {
      &:hover {
        background: var(--siniy);
        color: #fff;
      }
    }
  }

  .add-tags {
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    margin-bottom: 30px;
    .add-tag {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--belyy);
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 5px;
      border-radius: 8px;
      padding: 8px 10px;
      background: var(--siniy);
      cursor: pointer;
    }
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;

    @media (max-width: 1440px) {
      grid-template-columns: repeat(3, 1fr);
    }

    @media (max-width: 1200px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: 1fr;
    }
  }

  .catalog-actions {
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;

    .sort-select {
      max-width: 290px;
      position: relative;
      width: 100%;
      &:after {
        content: "";
        position: absolute;
        top: 45%;
        transform: translateY(-50%) rotate(-45deg);
        right: 20px;
        border-left: 2px solid #000;
        border-bottom: 2px solid #000;
        width: 8px;
        height: 8px;
      }
      select {
        width: 100%;
        border: 1px solid var(--divayder);
        border-radius: 8px;
        padding: 8px 24px;
        background: #fff;
        appearance: none;
      }
    }

    .grid-items {
      display: flex;
      align-items: center;
      gap: 7px;
      .grid-item {
        cursor: pointer;
      }
      .grid-item svg {
        pointer-events: none;
      }
      .grid-item svg path {
        transition: 0.3s ease;
      }
    }
  }

  .catalog-items-bottom {
    max-width: 260px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 20px;
    .link-more {
      border: 1px solid var(--siniy);
      border-radius: 8px;
      padding: 12px 40px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);
      transition: 0.3s ease;
      display: block;
      text-align: center;
      @media (any-hover: hover) {
        &:hover {
          background: var(--siniy);
          color: #fff;
        }
      }
    }

    .pagination {
      display: flex;
      gap: 4px;

      .btn {
        border: 1px solid var(--siniy);
        border-radius: 4px;
        width: 40px;
        height: 40px;
        display: grid;
        place-items: center;
        cursor: pointer;
        svg {
          pointer-events: none;
        }
      }

      .list {
        display: flex;
        gap: 4px;

        .list-number {
          width: 40px;
          height: 40px;
          display: grid;
          place-items: center;
          border: 1px solid var(--siniy);
          border-radius: 4px;
          transition: 0.3s ease;

          @media (any-hover: hover) {
            &:hover {
              background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
              border-color: transparent;
            }
          }
        }
      }
    }
  }

  .catalog-future-title {
    margin-top: 60px;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: clamp(22px, 3vw, 28px);
    text-transform: uppercase;
    color: var(--chernyy);
    margin-bottom: 30px;
  }

  .catalog-futures {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
    .catalog-future {
      display: grid;
      grid-template-columns: 100px 1fr;
      gap: 10px;
      .icon {
        border-radius: 8px;
        width: 100px;
        height: 100px;
        background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
        display: grid;
        place-items: center;
      }

      .content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 10px;
        .subtitle {
          font-family: var(--font-family);
          font-weight: 700;
          font-size: 16px;
          line-height: 120%;
          color: var(--chernyy);
        }
        .desc {
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 14px;
          line-height: 130%;
          color: var(--chernyy);
        }

        a {
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 14px;
          line-height: 130%;
          color: var(--siniy);
          border-bottom: 1px solid var(--siniy);
          padding-bottom: 4px;
          max-width: fit-content;
        }
      }
    }
  }

  .catalog-hit-title {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: clamp(22px, 3vw, 28px);
    text-transform: uppercase;
    color: var(--chernyy);
    margin-bottom: 30px;
    margin-top: 60px;
  }

  .questions__items {
    display: grid;
    gap: 10px;
  }

  .questions__item {
    border-radius: 8px;
    padding: 14px 20px;
    background: var(--fon);
    cursor: pointer;
    & > * {
      pointer-events: none;
    }
    .head {
      display: flex;
      justify-content: space-between;
      gap: 20px;
      align-items: center;

      .title {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: var(--chernyy);
      }

      .toggler {
        border: 1px solid var(--siniy);
        border-radius: 4px;
        width: 30px;
        height: 30px;
        display: grid;
        place-items: center;

        svg {
          transition: 0.3s ease;
        }
      }
    }

    .bottom {
      display: grid;
      grid-template-rows: 0fr;
      overflow: hidden;
      transition: 0.3s ease;

      .content {
        overflow: hidden;
        margin-top: 0;
        transition: 0.3s ease;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--chernyy);
      }
    }
  }

  .questions__item--active {
    .bottom {
      grid-template-rows: 1fr;
    }
    .content {
      margin-top: 20px;
    }

    .toggler svg {
      rotate: 45deg;
    }
  }
}

.catalog-cart {
  position: relative;
  display: flex;
  flex-direction: column;
  transition: 0.3s ease;
  border: 1px solid transparent;
  border-radius: 8px;
  padding-bottom: 10px;
  .link {
    position: absolute;
    inset: 0;
    z-index: 1;
  }

  .image {
    padding: 33%;
    margin-bottom: 20px;
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 8px;
    }
  }
  .title {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    color: var(--chernyy);
    padding-inline: 10px;
    margin-bottom: 14px;
  }

  .info {
    display: flex;
    align-items: center;
    gap: 10px;
    flex: 1 1 auto;
    margin-bottom: 24px;
    padding-inline: 10px;
    .price {
      display: flex;
      align-items: center;
      gap: 8px;

      .new {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: var(--chernyy);
      }
      .old {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        text-decoration: line-through;
        color: var(--vspomogatenye-elementy);
      }
    }
    .size {
      display: flex;
      align-items: center;
      gap: 8px;

      .new {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        color: var(--chernyy);
      }
      .old {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        text-decoration: line-through;
        color: var(--vspomogatenye-elementy);
      }
    }
  }

  .bottom {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    padding-inline: 10px;
  }

  .btn--gray {
    position: relative;
    z-index: 2;
    @media (any-hover: hover) {
      &:hover {
        background: var(--siniy);
        color: #fff;

        svg path {
          stroke: #fff;
        }
      }
    }
  }

  .link-more {
    background: transparent;
    border: 1px solid var(--siniy);
    border-radius: 8px;
    padding: 9px 20px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    display: block;
    text-align: center;
    transition: 0.3s ease;
  }

  @media (any-hover: hover) {
    &:hover {
      border: 1px solid var(--siniy);

      .link-more {
        background: var(--siniy);
        color: #fff;
      }
    }
  }
}
