.docs {
  &__wrapper {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: 40px;
  }

  .banner-section {
    margin-bottom: 20px;
  }

  .docs-tags__list {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
  }

  .docs-tags__list-btn {
    background: var(--fon);
    color: #000;
    @media (any-hover: hover) {
      &:hover {
        color: #fff;
      }
    }
    &.active {
      background: var(--siniy);
      color: #fff;
    }
  }

  .docs-items {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 10px;
    margin-top: 40px;
  }

  .docs-item {
    border-radius: 8px;
    padding: 14px;
    background: var(--fon);

    .docs-item-top {
      display: grid;
      grid-template-columns: 71px 1fr;
      align-items: center;
      gap: 10px;
      margin-bottom: 20px;
    }
    .actions {
      display: flex;
      flex-direction: column;
      gap: 10px;
      a {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        color: var(--chernyy);
        padding-bottom: 3px;
        border-bottom: 1px dashed #000;
        max-width: fit-content;
      }

      .buttons {
        display: flex;
        gap: 5px;

        a {
          border: none;
          min-width: 28px;
          height: 28px;
          display: grid;
          place-items: center;
          border-radius: 4px;
          padding: 0;
          &.copy {
            border: 1px solid var(--siniy);
          }
          &.download {
            background: var(--siniy);
          }
        }
      }
    }

    .title {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 14px;
      color: var(--chernyy);
    }
  }
}
