.search-result-hero {
  margin-bottom: 30px;
}

.result {
  &__row {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: 40px;
    margin-bottom: 20px;
    @media (max-width: 767px) {
      grid-template-columns: 1fr;
      gap: 10px;
    }
    .select {
      select {
        border: 1px solid var(--divayder);
        border-radius: 8px;
        padding: 14px 24px;
        background: var(--belyy);
        appearance: none;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--vspomogatenye-elementy);
        width: 100%;
      }
    }
  }

  &__search {
    position: relative;

    input {
      border: 1px solid var(--divayder);
      border-radius: 8px;
      padding: 15px 20px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--vspomogatenye-elementy);
      width: 100%;
    }

    &-btn {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 5px;
      width: 40px;
      height: 40px;
      display: grid;
      place-items: center;
      border-radius: 4px;
      background: var(--fon);
      transition: 0.3s ease;
      @media (any-hover: hover) {
        &:hover {
          background: var(--siniy);

          svg path {
            fill: #fff;
          }
        }
      }
    }
  }

  &__items {
    display: grid;
    gap: 8px;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    background: #fff;
    flex-wrap: wrap;
    padding-bottom: 8px;
    border-bottom: 1px solid var(--vspomogatenye-elementy);
    align-items: center;
    transition: 0.3s ease;
    @media (max-width: 767px) {
      flex-direction: column;
    }
    .left {
      display: grid;
      grid-template-columns: 220px 1fr;
      gap: 20px;
      @media (max-width: 767px) {
        grid-template-columns: 1fr;
      }

      img {
        width: 100%;
      }
    }

    .content {
      display: flex;
      flex-direction: column;
      gap: 8px;
      padding-top: 14px;

      span {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        color: var(--chernyy);
        border-radius: 8px;
        padding: 4px 16px;
        max-width: fit-content;
        background: var(--fon);
        display: block;
      }
      p {
        margin: 0;
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 14px;
        color: var(--chernyy);
        margin-bottom: 15px;
      }
    }
    @media (any-hover: hover) {
      &:hover {
        box-shadow: 0 1px 13px 0 rgba(30, 55, 91, 0.25);
      }
    }
  }

  .right {
    width: 150px;
    margin-left: auto;
    padding-right: 20px;
  }

  &__link {
    padding: 12px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    max-width: 150px;
    width: 100%;
    background: transparent;
    border: 1px solid var(--siniy);
    display: block;
    text-align: center;
    @media (any-hover: hover) {
      &:hover {
        color: #fff;
      }
    }
  }
}
