.article {
  margin-bottom: 60px;

  &__wrapper {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: clamp(20px, 3vw, 40px);
    align-items: flex-start;
    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__aside {
    border-radius: 8px;
    padding: 20px 10px;
    background: var(--fon);

    .subtitle {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      color: var(--chernyy);
      margin-bottom: 20px;
    }

    .list {
      display: flex;
      flex-direction: column;
      gap: 12px;

      a {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--chernyy);
        display: block;
        transition: 0.3s ease;
        border-radius: 6px;
        @media (any-hover: hover) {
          &:hover {
            background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
            padding: 8px 14px;
          }
        }
      }
    }
  }

  .block {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  .list {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-bottom: 40px;
    li {
      display: flex;
      flex-direction: column;
      gap: 4px;
      position: relative;
      padding-left: 16px;
      &:before {
        content: "";
        position: absolute;
        top: 5px;
        left: 0;
        width: 6px;
        height: 6px;
        background: var(--siniy);
        border-radius: 50%;
      }
    }
  }

  .article__slider-head {
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    flex-wrap: wrap;

    .article__slide-nav {
      display: flex;
      gap: 10px;
    }

    .article__slide-nav-btn {
      display: grid;
      width: 40px;
      height: 40px;
      place-items: center;
      border: 1px solid var(--siniy);
      border-radius: 4px;
      cursor: pointer;
    }
  }

  &__description {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
    border-radius: 8px;
    padding: 20px;
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .article__navigation {
    display: flex;
    justify-content: space-between;
    gap: 10px;
    flex-wrap: wrap;

    a {
      display: flex;
      align-items: center;
      gap: 10px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);
      border: 1px solid var(--siniy);
      border-radius: 8px;
      padding: 12px 40px;
      transition: 0.3s ease;

      @media (any-hover: hover) {
        &:hover {
          background: var(--siniy);
          color: #fff;

          svg path {
            stroke: #fff;
          }
        }
      }
    }

    .repost-btn {
      display: flex;
      align-items: center;
      gap: 10px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);

      .icon {
        border-radius: 4px;
        width: 40px;
        height: 40px;
        background: var(--fon);
        display: grid;
        place-items: center;
        transition: 0.3s ease;
      }

      @media (any-hover: hover) {
        &:hover {
          .icon {
            background: var(--siniy);
            svg path {
              stroke: #fff;
            }
          }
        }
      }
    }
  }
}
